@import url(https://fonts.googleapis.com/css?family=Overpass);
@import url(https://fonts.googleapis.com/css?family=Muli:regular,bold,light&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Muli:regular,bold,light&subset=latin,latin-ext);
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Muli', sans-serif;
}

code {
  font-family: 'Muli', sans-serif;
}

.App {
  text-align: center;
  overflow-x: hidden;
  height: 100vh;
  width: 100wh;
}

/**************** PAGE SETUP ****************/
.page-background {
    font-family: 'Overpass', sans-serif;
    height: 100vh; 
    width: 100vw;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #d5ebf6;
}

.page-header {
    font-weight: bold;
    color: #3b9bc2;
    text-align: left;
    font-size: 5vh;
    padding-top: 7vh;
    padding-bottom: 1vh;
    padding-left: 8vw;
}

.content-background {
    font-family: 'Overpass', sans-serif;
    background: white;
    display: block;
    margin-left: auto;
    margin-right: auto;
    align-content: center;
    justify-content: center;
    width: 90vw;
    height: 75vh;
}

/********************* LISTS ********************/
.list {
    overflow: scroll; 
    height: 75%;
}

/******************* BUTTONS ********************/
.button {
    font-weight: bold;
    font-size: 2.5vh;
    color: #3b9bc2;
    display: block;
    align-content: center;
    justify-content: center;
    margin-top: 4vh;
    border: none;
    background-color: #d5ebf6;
    width: 14vh;
    height: 6vh;
    box-shadow: 3px 3px #cdcdcd;
    border-radius: 4px;
    margin: 2%;
}

.nav {
    justify-content: center;
    margin: auto;
    display: flex;
    flex-direction: row;
    margin-top: 3vh;
}

.button:hover{
    background-color: #c9e0ec;
}

.delete-button {
    border: none;
    justify-content: center;
    color: #d5ebf6;
    font-size: 3vh;
  }
  
.delete-button:hover{
    color: #91bcd1;
}

/***************** INPUTS ****************/
::-webkit-input-placeholder{
    color: #d5ebf6;
    font-size: 2vh;
}
::-ms-input-placeholder{
    color: #d5ebf6;
    font-size: 2vh;
}
::placeholder{
    color: #d5ebf6;
    font-size: 2vh;
}

:focus {
    outline: none;
}

/***************** CATEGORY INPUTS ****************/
.category-lists {
    height: 75%;
    width: inherit;
    display: flex;
    flex-direction: row;
}

.award-list {
    text-align: left;
    width: 33%;
}

.award {
    position: relative;
    left: 4vw;
    font-size: 22px;
    color: #3B9BC2;
    width: 65%;
    margin-right: 0;
}

.award-list-name {
    position: relative;
    color: #3B9BC2;
    left: 4.3vw;
    display: inline-block;
    font-size: 2.5vh;
    font-weight: bolder;
    padding: 2%;
    padding-top: 5%;
}

.award-list-element {
    display: flex;
    flex-direction: row;
    padding-right: 20px;
}

.award-list-field {
    position: relative;
    text-align: center;
    width: 100%;
}

.award-list-input {
    height: 4vh;
    width: 70%;
    font-size: 2vh;
    border: none;
    border-bottom: 2px solid #8AC9E1;
    border-top: 2px solid #8AC9E1;
    color: #d5ebf6;
}

.award-list-item {
    width: 100%;
}

/********************* DATA ENTRY ********************/
.data-button {
    margin-top: 8vh;
}

.data-entry-contents {
    padding-top: 9vh;
    margin-bottom: 3vh;
}

.data-entry-element {
    width: 40%;
    display: flex;
    align-items: center;
    margin: auto;
    margin-top: 3vh;
    justify-content: space-between;
}

.data-element-title {
    margin: 10px;
    font-size: 2.5vh;
    font-weight: bold;
    color: #3b9bc2;
}

.data-entry-input{
    height: 4vh;
    width: 40%;
    font-size: 2vh;
    border: none;
    border-bottom: 2px solid #8AC9E1;
    border-top: 2px solid #8AC9E1;
    color: #d5ebf6;
}

.upload-file {
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.upload-file + label{
    cursor: pointer;
    font-weight: bold;
    font-size: 2vh;
    color: #3b9bc2;
    border: none;
    background-color: #d5ebf6;
    width: 22vh;
    width: 200px;
    height: 25px;
    box-shadow: 3px 3px #cdcdcd;
    border-radius: 4px;
    margin: 0;
    padding: 2%;
}

/********************* JUDGE INFO ********************/
.judge-input-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 80%;
}

.judge-input {
    margin-top: 2vh;
    width: 35%;
    display: inline-block;
    margin-left: 5vh;
    align-content: left;
    text-align: left;
}

.judge-name-title {
    margin-top: 2.5vh;
    margin-bottom: 1vh;
    font-weight: bold;
    color: #3b9bc2;
    font-size: 3vh;
    text-align: left;
}

.judge-name-input {
    height: 4vh;
    width: 100%;
    font-size: 2vh;
    border: none;
    border-bottom: 2px solid #8AC9E1;
    border-top: 2px solid #8AC9E1;
    color: #d5ebf6;
}

.judge-api-title {
    margin-top: 3vh;
    margin-bottom: 1vh;
    font-weight: bold;
    color: #3b9bc2;
    font-size: 2.5vh;
    text-align: left;
}

.judge-dropdown {
    display: block;
    width: 100%;
    margin-bottom: 10%;
}

.judge-list {
    padding: 4%;
    padding-top: 3%;
    position: relative;
    display: inline-block;
    align-content: center;
    overflow: auto;
    width: 65%;
}
  
.judge-list-name {
    display: inline-block;
    font-weight: bold;
    color: #3b9bc2;
    background-color: #98d0ec;
    font-size: 2.5vh;
    text-align: left;
    padding: 1%;
    padding-left: 5%;
    width: 44%;
}

.judge-list-api {
    display: inline-block;
    font-weight: bold;
    color: #3b9bc2;
    background-color: #98d0ec;
    font-size: 2.5vh;
    text-align: left;
    padding: 1%;
    padding-left: 5%;
    width: 44%;
}

ul {
    padding: 0;
    margin: 0;
}

.judge-button {
    margin: -4vh;
}
  
.judge-item {
    display: flex;
    flex-direction: row;
    border-bottom: 2px solid #d5ebf6;
}

.dark-color {
    background-color: #d5ebf6;
}
  
.light-color {
    background-color: white;
}
  
.judge {
    width: 100%;
}
  
.judge-api {
    position: relative;
    display: inline-block;
    color: #3b9bc2;
    font-size: 2.3vh;
    text-align: left;
    width: 45%;
    padding: 0.5%;
    padding-left: 7%;
}
  
.judge-name {
    position: relative;
    display: inline-block;
    color: #3b9bc2;
    font-size: 2.3vh;
    text-align: left;
    width: 45%;
    padding: 0.5%;
}
.j-login-container{
    font-family: 'Muli', sans-serif;
    height: 100vh;
    width: 100%;
    text-align: left;
    display: inline-block;
    justify-content: center;
    align-items: center; 
    flex-direction: column;
}

.j-login-components {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}

.j-login-dropdown {
    width: 70%;
    padding: 2vh;
    margin-top: 2vh;
}

.j-login-title {
    font-weight: bolder;
    color: #3B9BC2;
    font-size: 4vh;
    margin-bottom: 3vh;
}

.j-login-password {
    border: none;
    width: 70%;
    color: #98d0ec;
    margin-top: 2vh;
    border-top: 2px solid #98d0ec;
    border-bottom: 2px solid #98d0ec;
    height: 5%;
    font-family: 'Muli';
}

.j-login-button {
    font-family: 'Muli';
    font-weight: bold;
    font-size: 2.5vh;
    color: white;
    display: block;
    align-content: center;
    border-radius: 5px;
    justify-content: center;
    margin-top: 4vh;
    border: none;
    background-color: #3B9BC2;
    width: 70%;
    height: 6vh;
    box-shadow: 0 0px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 3px 0 rgba(0, 0, 0, 0.19);
}

input {
    color: #98d0ec;
    font-size: 2vh;
    padding: 0;
}

.j-login-password::-webkit-input-placeholder{
    padding-left: 10px;
}

.j-login-password::-webkit-input-value {
    padding-left: 10px;
}


html,
body,
div,
p {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

.instructions {
    font-family: 'Muli';
    position: relative; 
    background: white;
    height: 100vh;
    width: 70%;
    display: inline-block;
    justify-content: center;
    align-items: center; 
}

.instructions-content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}

.instructions-title {
    font-family: 'Muli', sans-serif;
    font-weight: bolder;
    color: #3B9BC2;
    font-size: 5vh;
}

.instructions-text {
    margin-top: 1vh;
    font-family: 'Muli', sans-serif;
    font-weight: light;
    font-size: 2.5vh;
    color: black;
    text-align: left;
}

.instructions-next {
    font-family: 'Muli';
    font-weight: bolder;
    font-size: 2.5vh;
    color: white;
    display: block;
    align-content: center;
    justify-content: center;
    margin-top: 3vh;
    border: none;
    background-color: #3B9BC2;
    width: 50%;
    height: 6vh;
    box-shadow: 0 0px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 3px 0 rgba(0, 0, 0, 0.19);
}
.scoring-view {
    font-family: 'Muli', sans-serif;
    background-color: white;
    color: #3B9Bc2; 
    display: block;
    height: 100vh;
    padding-top: 10px;
}

.score-button-box {
    height: 60px;
    border-bottom: 1px solid #8AC9E1;
    margin: auto;
    padding: auto;
}

.score-button {
    border-radius: 5px;
    text-align: center;  
    margin: auto;
    margin-top: 20px;
    background-color: #8AC9E1;
    padding: 3px 4px;
    font-size: 15px;
    width: 200px;
    display: flex;
    flex-direction: row;
}

.unscored {
    padding: 5px 5px;
    font-size: 15px;
    background-color: white;
    cursor: pointer;
    color: #3B9Bc2;
    border: 1px solid #8AC9E1;
    width: 100px;
}

.scored {
    padding: 5px 5px;
    font-size: 15px;
    background-color:  #8AC9E1;
    cursor: pointer;
    color: white;
    border: 1px solid #8AC9E1;
    width: 100px;
}

.projectcmp {
    display: flex;
    flex-basis: auto;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid #8AC9E1;
    height: 65px;
    text-align: left;
}

.project-text {
    padding-left: 20px;
}

h2 {
    margin: 0;
}

h3 {
    margin: 2.5px;
    margin-top: 10px;
}

p {
    margin: 2.5px;
    font-size: 12px;
}

.overview-score {
    width: 31px;
    text-align: center;
    top: 0;
    right: 0;
    margin: 12px;
    border-radius: 50%;
    border: 4px solid #8AC9E1;
}

.projID {
    margin-top: 5%;
    margin-bottom: 5%;
}
.entirePage {
    background-color: white;
    color: #3b9bc2;
    height: 100%;
    width: 100%;
    text-align: left;

}
.attribute-item {
    position: relative;
    height: 33%;
    width:100%;
    padding: 2%;
}

.projinfo-icon {
    left: 0%;
    top: 0%;
    position: relative;
}

.projHeader {
    font-size: 2.5vh;
    text-align: left;
    position: relative; 
    left: 0%;
    top: 0%;
}

.projTitle{
    font-size: 300%;
    font-weight: bold;
}
.projID {
    color: #8AC9E1;
    font-size: 150%;

}
.projDetails {
    position: relative; 
    top: 4%;
    left: 0%;
}

.projInfo {
    position: relative;
    padding-top: 10vh;
    width:auto;
    height:70%;
    padding-left: 10vh;

}

.detail-header {
    font-weight: bold;
    font-size:5vh;
    position: relative;
    padding-left: 3%;
}


.score {
    position: relative;
    text-align: center;
    width:20vw;
    height:20vw;
    z-index:1;
    bottom:10%;
    left:40%;
}

.actual-info {
    position: relative; 
    margin: 0%;
    left: 14%;
    font-size: 3vh;
}
.scoreInput {
    text-align: center;
    border: 3px solid #3b9bc2;
    border-radius: 100%;
    width: 10vh;
    height:10vh;
    z-index:0;
}

.head-and-icon {
    position: relative;
    display: flex;
    align-items: center;
}

::-webkit-input-placeholder {
    font-size:1.5vh;
}

::-ms-input-placeholder {
    font-size:1.5vh;
}

::placeholder {
    font-size:1.5vh;
}

.backButton {
    position: relative;
    top: 3vh;
    left: 3vh;
    width: 7vw;
    height: 7vw;
    border:none;
    background-color:transperant;
    outline:none;

    cursor:'pointer';
}

